import { CustomActionParams } from './types';

export const customAction = (params: CustomActionParams<any>) => {
  const { sdk, action, actionParams } = params;

  const actionFunc = sdk.overrides?.actions?.[action.id];
  if (!actionFunc) {
    return null;
  }

  return actionFunc({
    action,
    actionParams,
    sdk,
  });
};
